.shape {
  $size: 40vw;
  font-family: var(--heading-font);
  position: absolute;
  &-left {
    background-color: #03a9f5;
    width: $size;
    height: $size * 0.75;
    top: 0 !important;
    left: 0 !important;
    border-radius: 0 calc(#{$size} * 0.1) calc(#{$size} * 0.1) 0;
    transform: rotate($rot) translateX(-40%);
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 10%;
    color: #fff;
    .mobile & {
      flex-direction: column;
      justify-content: center;
      opacity: 0;
      padding-right: 0;
    }
    // opacity: 0;
    // visibility: hidden;
  }
  &-right {
    $size: 60vh;
    height: $size;
    width: $size;
    background-color: #f030b5;
    bottom: 5vh;
    right: 0;
    border-radius: 50%;
    transform: translateX(75%);
    .mobile & {
      display: none;
    }
  }
}
.shape-bg {
  position: absolute;
}
.shape-bg3 {
  background: img('shape-3.png') no-repeat;
  height: 69px;
  width: 67px;
  top: 22vh;
  left: 70vw;
  animation: shape3 30s linear infinite;
  @keyframes shape3 {
    to {
      transform: rotate(360deg);
    }
  }
}
.shape-bg1 {
  background: img('shape-1.png') no-repeat;
  height: 52px;
  width: 51px;
  top: 20vh;
  left: 45vw;
  animation-delay: 0.2s;
  .mobile & {
    top: 17vh;
    left: 15vw;
  }
}
.shape-bg5 {
  background: img('shape-5.png') no-repeat;
  height: 87px;
  width: 24px;
  top: 50vh;
  left: 85vw;
  animation-delay: 0.2s;
  .mobile & {
    top: 17vh;
    left: 15vw;
    display: none;
  }
}
.shape-bg2 {
  background: img('shape-6.png') no-repeat;
  height: 87px;
  width: 44px;
  top: 82vh;
  left: 30vw;
  animation-delay: 0.2s;
  animation: shape2 20s linear infinite alternate;
  transform: translateY(0);
  .mobile & {
    top: 75vh;
    left: 20vw;
  }
  @keyframes shape2 {
    to {
      transform: translateY(-7.5vh);
    }
  }
}
.shape-bg4 {
  background: img('shape-4.png') no-repeat;
  height: 67px;
  width: 68px;
  top: 75vh;
  left: 60vw;
  animation-delay: 0.2s;
  animation: shape2 20s linear infinite;
  transform: translateY(0);
  .mobile & {
    top: 65vh;
    left: 70vw;
  }
  @keyframes shape2 {
    33% {
      transform: translateX(3vh) translateY(-1.5vh);
    }
    66% {
      transform: translateX(0vh) translateY(-3vh);
    }
    100% {
      transform: translateX(0vh) translateY(0vh);
    }
  }
}

.shape-right {
  overflow: hidden;
  color: var(--body-bg);
  display: flex;
  justify-content: flex-start;
  align-items: center;
  svg {
    fill: var(--body-bg);
  }
}

.shapeModalRightContainer {
  width: 75%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding-right: 1.5em;
  opacity: 0;
  animation: showShape var(--animateDiration) var(--animateDiration) forwards;
}
.contact-data-title {
  $mb: 1em;
  font-size: 2em;
  padding-bottom: 1em;
  position: relative;
  margin-bottom: 1em;
  &::after {
    content: '';
    position: absolute;
    height: 3px;
    width: 50%;
    background-color: var(--body-bg);
    bottom: 0;
    right: 0;
  }
}

.modalContactData {
  @extend %list-style;
  text-align: right;
  font-size: 1.3em;
  li {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      display: block;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background: white;
      max-width: 0;
      transition: max-width 0.3s;
    }
    &:hover {
      &::before {
        max-width: 100%;
      }
    }
    > a::after {
      content: '';
      display: inline-block;
      position: relative;
      top: 2px;
      width: 1em;
      height: 1em;
      margin-left: 0;
      background: brown;
    }

    &:first-child {
      > a::after {
        background: img('whatsapp.svg') center / cover no-repeat;
      }
    }
    &:last-child {
      > a::after {
        background: img('envelope1.svg') center / cover no-repeat;
      }
    }
  }
  a {
    transition: color 0.3s;
    &:hover {
      // color: var(--color-main);
    }
  }
}

.social-fullscreen-right {
  $height: 1.4em;
  @extend %list-style;
  display: flex;
  height: $height;
  margin-top: 0.75em;
  opacity: 0;
  animation: showShape var(--animateDiration) var(--animateDiration) forwards;
  li,
  a {
    display: block;
    width: $height;
    height: $height;
    .mobile & {
      width: $height * 1.5;
      height: $height * 1.5;
    }
  }
  li {
    // background-color: limegreen;
    margin-right: 1em;
    &:last-child {
      margin-right: 0;
    }
  }
  a {
    display: flex;
    justify-content: center;
    align-items: center;
    // background: lime;
  }
  svg {
    width: auto;
    height: 100%;
    max-width: 100%;
    fill: #fff;
  }
}

@keyframes showShape {
  to {
    opacity: 1;
  }
}
