:root {
  --footer-height: #{rem(50px)};
  --header-height: #{rem(75px)};
  --item-skill-width: 0;
  &.mobile,
  &.ipadPro {
    --header-height: #{rem(75px)};
  }
  // @include from(l) {
  //   --header-height: 4rem;
  // }
}

// z-ndex
$z-back: -10;
$z-normal: 1;
$z-tooltip: 10;
$z-fixed: 100;
$z-modal: 1000;

:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}
