.ed-grid {
  display: grid;
  grid-template-columns: 100%;
  grid-column-gap: var(--gap);
  width: calc(100% - 2rem);
  margin-left: 1rem;
  margin-right: 1rem;
  max-width: var(--max-width);
}

.ed-grid.gap-0 {
  --gap: 0rem;
}

.ed-grid.gap-1 {
  --gap: 0.5rem;
}

.ed-grid.gap-2 {
  --gap: 1rem;
}

.ed-grid.gap-3 {
  --gap: 1.5rem;
}

.ed-grid.gap-4 {
  --gap: 2rem;
}

@media screen and (min-width: 1200px) {
  .ed-grid {
    width: 100%;
    margin-left: auto;
    margin-right: auto;
