  font-size: 1.2em;
}

.inicio__title {
  font-size: 2.5vw;
}

.inicio__title span {
  background: linear-gradient(to right, #f033b5, #f271b2);
  -webkit-background-clip: text;
  color: transparent;
}

.mobile .inicio__title {
  font-size: 1.7em;
}

.to-start {
  position: absolute;
  bottom: 1em;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}

.to-start img {
  display: inline-block;
  margin-left: 0.5em;
  height: auto;
  width: 1em;
}

.nosotros {
  height: auto;
}

.nosotros__container {
  padding: 5vh 1em !important;
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 1024px) {
  .nosotros__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2em;
  }
}

.nosotros__text {
  order: 1;
  width: 100%;
}

@media screen and (min-width: 1024px) {
  .nosotros__text {
    order: initial;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
}

.nosotros__vector {
  order: 0;
  margin-bottom: 2em;
}

@media screen and (min-width: 1024px) {
  .nosotros__vector {
    order: initial;
  }
}

.nosotros__btn {
  display: table;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 1024px) {
  .nosotros__btn {
    display: inline-block;
    margin-left: initial;
    margin-right: initial;
  }
}

.skill {
  margin-bottom: 5vh;
}

@media screen and (min-width: 640px) {
  .skill {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 2em;
  }
}

@media screen and (min-width: 1024px) {
  .skill {
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 4em;
  }
}

.skill-item {
  display: grid;
  grid-template-rows: calc(var(--item-skill-width) * 0.6) auto;
  margin-bottom: 2em;
}

.skill-item:hover .skill-circle {
  box-shadow: 0 0 0 3px var(--shadow-color), 0 0 0 8px var(--bg-color);
}

@media screen and (min-width: 1024px) {
  .skill-item {
    margin-bottom: 0;
  }
}

.skill-item:nth-child(1) .skill-circle {
  --bg-color: #03a9f5;
}

.skill-item:nth-child(2) .skill-circle {
  --bg-color: #41526b;
}

.skill-item:nth-child(3) .skill-circle {
  --bg-color: #9556f5;
}

.skill-item:nth-child(4) .skill-circle {
  --bg-color: #f021b6;
}

.skill-vector {
  position: relative;
}

.skill-circle {
  --shadow-color: #fff;
  position: absolute;
  display: block;
  width: calc(var(--item-skill-width) * 0.4);
  height: calc(var(--item-skill-width) * 0.4);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--bg-color);
}

@media screen and (max-width: 639px) {
  .skill-circle {
    box-shadow: 0 0 0 3px var(--shadow-color), 0 0 0 8px var(--bg-color);
  }
}

.skill-circle svg {
  fill: #fff;
  display: block;
  width: calc(var(--item-skill-width) * 0.15);
  height: calc(var(--item-skill-width) * 0.15);
}

.skill-text {
  font-size: 1.3em;
  text-align: center;
}

.portafolio__container {
  padding: 5vh 1em !important;
  min-height: 50vh;
  display: flex;
  align-items: center;
}

.servicios {
  background: linear-gradient(to right, #237AFE 2%, #03A9F5 82%);
  color: var(--body-bg);
}

.servicios__container {
  padding: 5vh 1em !important;
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 1024px) {
  .servicios__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 4em;
  }
}

.servicios__text-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

@media screen and (max-width: 1023px) {
  .servicios__text-container {
    order: 0;
    margin-bottom: 2em;
  }
}

.servicios__group {
  margin-bottom: 1.3em;
}

.servicios__group:last-child, .servicios__group:last-of-type {
  margin-bottom: 0;
}

.servicios__title {
  font-weight: 700;
  margin-bottom: 0;
}

@media screen and (max-width: 1023px) {
  .servicios__vector {
    order: 1;
  }
}

.contacto__container {
  padding: 5vh 1em !important;
  min-height: calc(100vh - var(--header-height));
}

.hidden {
  opacity: 0;
  visibility: hidden;
}
