.main-menu {
  @extend %list-style;
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: var(--body-bg);
  text-transform: capitalize;
  a {
    display: block;
    padding: 0 1.2em;
    line-height: 3em;
  }
}
.main-nav {
  height: inherit;
}

.menu-fullscreen,
.social-fullscreen {
  @extend %list-style;
  transform: rotate($rot * -1);
  display: flex;
  flex-direction: column;
  .mobile & {
    transform: none;
  }
}

.mobile .menu-fullscreen {
  margin-bottom: 1.5em;
}
.menu-fullscreen {
  $height: 2rem;
  $circle: 12px;
  $width: 2px;
  font-size: 1.75em;
  text-transform: capitalize;
  font-family: var(--heading-font);
  opacity: 0;
  animation: showShape var(--animateDiration) var(--animateDiration) forwards;
  li {
    position: relative;
    margin-bottom: $height;
    &::after,
    &::before {
      position: absolute;
      content: '';
      display: block;
      left: -1em;
      top: 50%;
    }
    &::after {
      width: $circle;
      height: $circle;
      border: $width solid var(--body-bg);
      transform: translateY(-50%);
      border-radius: 50%;
      background: #03a9f5;
      transition: background 0.3s;
    }
    &::before {
      width: $width;
      height: $height * 2.3;
      background: var(--body-bg);
      transform: translateX(calc(#{$circle/2} - 0.5px)) translateY($circle/2);
    }
    &:last-child {
      margin-bottom: 0;
      &::before {
        display: none;
      }
    }
    &:hover::after {
      background: var(--body-bg);
    }
  }
}
