.btn {
}
.btn {
  display: inline-block;
  line-height: 3em;
  background-color: var(--color-main);
  padding: 0 1em;
  border-radius: 1em;
  margin-top: 1.5em;
  transition: all 0.3s;
  color: #fff;
  &:hover {
    background-color: var(--color-main-hover);
  }
  &.second-color {
    background: linear-gradient(
      to right,
      var(--color-second),
      var(--color-second-hover)
    );
    &:hover {
      --color-second-hover: #{darken(#f021b6, 20)};
    }
  }
}

