*,
*::before,
*:after {
  box-sizing: border-box;
}

html,
body {
  width: calc(100vw - var(--scrollbar));
  margin: 0;
  scroll-behavior: smooth;
}

// .mobile,
// .mobile body {
//   width: 100% !important;
// }

body {
  // font-family: $body-font;
  background: var(--body-bg);
  color: var(--font-color);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  font-size: 16px;
  transition: opacity 0.5s;
  .mobile & {
  }
}

a {
  color: inherit;
  text-decoration: none;
  &:hover {
    text-decoration: none;
  }
}

img,
picture,
picture img {
  display: block;
  max-width: 100%;
  height: auto;
}

section,
figure,
article {
  margin: 0;
  padding: 0;
}
p {
  margin-bottom: 1em;
  &:last-child,
  &:only-child,
  &:last-of-type,
  &:only-of-type {
    margin-bottom: 0;
  }
}
