.toggle-menu {
  $size: 3em;
  $line-height: 2px;
  $color-line: #fff;
  height: $size;
  width: $size;
  position: relative;
  cursor: pointer;
  transition: all var(--animateDiration);
  opacity: 1;
  span {
    position: absolute;
    height: $line-height;
    width: 70%;
    background-color: $color-line;
    left: 15%;
    top: 50%;
    transform-origin: center;
    transform: translateY(-50%);
    transition: all 0.5s;
  }
  .span1 {
    max-width: 70%;
    top: 30%;
  }
  .span2 {
    max-width: 40%;
  }
  .span3 {
    max-width: 50%;
    top: 70%;
  }
  &:hover {
    .span1 {
      max-width: 50%;
    }
    .span2 {
      max-width: 70%;
    }
    .span3 {
      max-width: 30%;
    }
  }
  &.active {
    .span1 {
      max-width: 70%;
      top: 50%;
      transform: translateY(-50%) rotate(45deg);
    }
    .span2 {
      opacity: 0;
    }
    .span3 {
      max-width: 70%;
      top: 50%;
      transform: translateY(-50%) rotate(-45deg);
    }
  }
}
.main-header {
  background: transparent;
  display: grid;
  grid-template-columns: 1fr var(--max-size) 1fr;
  grid-template-areas: 'nav header social';
  height: var(--header-height);
  max-height: var(--header-height);
  transition: all 0.3s;
  padding: 0 5vw;
  width: 100%;
  max-width: calc(100vw - var(--scrollbar));
  position: fixed;
  z-index: var(--z-fixed);
  &.fixed {
    background: #3264f5;
    background: linear-gradient(to right, darken(#3264f5, 20) 2%, #3264f5 82%);
  }
  .mobile & {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
  }
  &__container {
    grid-area: header;
    display: grid;
    grid-template-columns: 30% 1fr;
    height: var(--header-height);
    .mobile & {
      display: block;
    }
  }
  .social-network {
    grid-area: social;
  }
  &__logo {
    display: flex;
    height: var(--header-height);
    align-items: center;
    img {
      width: auto;
      height: 60%;
    }
  }
}
.toggle {
  grid-area: nav;
  display: grid;
  align-items: center;
}
.social-network {
  $size: 1.1em;
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: #fff;
  .social {
    @extend %list-style;
    display: flex;
    &__item {
      height: $size;
      width: $size;
      margin-right: 0.8em;
      &:last-child {
        margin-right: 0;
      }
      &:first-child {
        margin-right: 0.5em;
      }
    }
    &__link {
      display: flex;
      justify-content: center;
      width: inherit;
      height: inherit;
      opacity: 0.8;
      transition: opacity 0.3s;
      &:hover {
        opacity: 0.9;
      }
      svg {
        width: auto;
        height: 100%;
        stroke: #fff;
        fill: #fff;
      }
    }
  }
}

.opacity {
  opacity: 0 !important;
}
