.modal {
  background: rgba(#000, 0.75);
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: fixed;
  z-index: var(--z-modal);
  top: 0;
  left: 0;
  padding: 1vh 5vw;
  // visibility: hidden;
  transition: all var(--animateDiration);
  opacity: 0;
  animation: showModal var(--animateDiration) forwards;
  @keyframes showModal {
    to {
      opacity: 1;
    }
  }
  & > .shape-left {
    animation: moveleft var(--animateDiration) var(--animateDiration) forwards;
    @keyframes moveleft {
      to {
        transform: rotate($rot) translateX(-20%);
      }
    }
    .mobile & {
      transform: none;
      animation: none;
      width: 100% !important;
      height: 100% !important;
      opacity: 1;
      border-radius: 0;
    }
  }
  & > .shape-right {
    animation: moveright var(--animateDiration) var(--animateDiration) forwards;
    @keyframes moveright {
      to {
        transform: translateX(25%);
      }
    }
  }
  &__close {
    span {
      max-width: none !important;
      top: 50% !important;
    }
    .span1 {
      transform: translateY(-50%) rotate(45deg) !important;
    }
    .span2 {
      transform: translateY(-50%) rotate(-45deg) !important;
    }
  }
}
.close {
  $size: 3em;
  $line-height: 2px;
  $color-line: #fff;
  position: absolute;
  height: $size;
  width: $size;
  z-index: calc(var(--z-modal) + 1000);
  .mobile & {
    top: 2vh;
    right: 5vw;
  }
}
